.project-cards-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 44px;
    align-self: center;
    justify-content: center;
}

.project-card {
    width: 500px;
    min-height: 550px;
    background: #fff;
    box-shadow: 10px 40px 50px rgba(5, 33, 146, 0.4);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    margin: 20px;
}

.project-card:hover {
    box-shadow: 10px 40px 50px rgba(3, 16, 70, 0.4);
    transition: all 0.3s;
}

.image-container {
    width: 100%;
    border-radius: 20px 20px 0 0;
}

.project-external-link {
    border-radius: inherit;
}

.project-image {
    display: block;
    border-radius: inherit;
    height: 290px;
    width: 500px;
}

.project-details-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-self: flex-start;
    padding: 20px;
}

.project-yt-link {
    font-weight: 600;
    color: #4089ed;
}

.project-yt-link-1 {
    font-weight: 600;
    color: #4089ed;
    margin-left: 40px;
}

.project-heading {
    margin: 0;
    font-size: 25px;
}

.project-details-container>p {
    font-weight: 300;
    font-size: 16px;
    line-height: 28px;
    color: #7d7987;
}

@media only screen and (max-width: 640px) {
    .project-cards-container {
        padding: 20px;
    }
    .project-card {
        width: 290px;
        min-height: 500px;
    }
    .project-image {
        display: block;
        border-radius: inherit;
        height: 160px;
        width: 290px;
    }
    .project-card:hover {
        box-shadow: 10px 40px 50px rgba(3, 16, 70, 0.4);
        transition: all 0.3s;
    }
    .project-details-container>p {
        font-weight: 200;
        font-size: 13px;
        line-height: 28px;
        color: #7d7987;
    }
    .project-heading {
        margin: 0;
        font-size: 20px;
    }
}